<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form">
            <Form ref="queryForm" :model="queryForm" :rules="validate">
                <Form-item class="query-item">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </Form-item>
                <Form-item prop="mobile" class="query-item">
                    <Input v-model="queryForm.mobile" placeholder=""></Input>
                </Form-item>
                <Form-item prop="email" class="query-item">
                    <AutoComplete v-model="queryForm.email" :data="emailList" @on-search="selectEmail" @on-select="setEmail" placeholder=""></AutoComplete>
                </Form-item>
                <Form-item class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </Form-item>
                <Form-item class="query-item">
                    <Select v-model="queryForm.enabledState" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="-1"></Option>
                    </Select>
                </Form-item>
                <Form-item class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </Form-item>
            </Form>
            <div class="clearfix"></div>
        </div>
        <!--  -->
        <div class="m-operation">
            <router-link :to="{ name: 'StoreAccount' }" class="operation-btn ivu-btn ivu-btn-primary"></router-link>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="warning" @click="deleteData"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(1)"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="warning" @click="enableOrDisable(-1)"></Button>
            <div class="clearfix"></div>
        </div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>            
            <!--  -->
            <Table
                :row-class-name="rowClassName"
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
            <!--  -->
            <Page
                class-name="m-page"
                show-elevator
                show-sizer
                show-total
                :total="page.dataCount"
                :page-size="page.pageSize"
                :current="page.pageNo"
                :page-size-opts="page.pageSizeOpts"
                @on-change="changePage"
                @on-page-size-change="changePageSize"
            >
            </Page>
            <div class="clearfix"></div>
        </div>
    </div>
</template>

<script>
    // 
    import Loading from '@/components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // Api
    // import Api from '@/api/api.js'
    // 
    import EmailComplete from 'mixins/email_complete.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [ EmailComplete, TableQuery, TableOperate, Page ],
        // computed: {
        //     // 
        //     apiGetAll(){
        //         return () => Api.GetAccList(this.page.pageNo, this.page.pageSize);
        //     },
        //     // 
        //     apiGetFilter(){
        //         return () => Api.FilterAccList(this.queryForm, this.page.pageNo, this.page.pageSize);
        //     },
        //     // 
        //     apiDelete(){
        //         return () => Api.DeleteAcc(this.selectList);
        //     },
        //     // 
        //     apiEnable(){
        //         return () => Api.EnableAcc({ enabledState: 1 }, this.selectList);
        //     },
        //     // 
        //     apiDisable(){
        //         return () => Api.EnableAcc({ enabledState: -1 }, this.selectList);
        //     }
        // },
        data() {
            return {
                // 
                pageLoading: false,
                // 
                jobList: [],
                // 
                provinceList: [],
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    mobile: '',
                    // 
                    email: '',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                    // 
                    enabledState: '',
                },
                // 
                validate: {
                    email:[{ pattern: Common.regEmail , message: '' }],
                    mobile:[{ pattern: Common.regMobile , message: '', trigger: 'blur' }],
                    date:[{
                        validator: (rule, value, callback) => {
                            Validate.ValidRangeDate(this.queryForm.sTime, this.queryForm.eTime, callback, false);
                        },
                        trigger: 'change',
                    }]
                },
                // 
                userList:[
                    {
                        title: '',
                        type: 'selection',
                        key: 'selectAll',
                        width: '60',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'realname',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'gender',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'mobile',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'email',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'createTime',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'enabledState',
                        align: 'center',
                        render: (h, params) => {
                            return h('span', {
								attrs: {
									class: params.row.enabledState == 1 ? 'status-success' : 'status-fail',
								}
							}, params.row.enabledState == 1 ? '' : '');
                        }
                    },
                    {
                        title: '',
                        width: '120',
                        key: 'operation',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                tableSetting.gotoDetail(h, params, this, '', 'StoreAccount'),
                            ]);                          
                        }
                    }
                ],
                //
                listData:[
                    {
                        id: 1,
                        realname: 'xLong',
                        gender: '',
                        mobile: '18376686974',
                        email: 'by_xlong@yeah.net',
                        createTime: '2018-2-28',
                        enabledState: 1
                    },
                    {
                        id: 2,
                        realname: 'Lio.Huang',
                        gender: '',
                        mobile: '17777075292',
                        email: 'lio.huang@qq.com',
                        createTime: '2018-2-28',
                        enabledState: 1
                    },
                    {
                        id: 2,
                        realname: '',
                        gender: '',
                        mobile: '13737147607',
                        email: 'xiaojian@zhujia.com',
                        createTime: '2018-2-28',
                        enabledState: 1
                    }
                ],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/AccountManage/List' }
            ]);
            // 
            this.getTableList();
        },
        methods: {
            // 
            getTableList(query){
                // this.pageLoading = true;
                // 
                if(query){
                    this.isQuery = true;
                    this.getFilterList();
                }
                else{
                    this.isQuery = false;
                    this.getAllList();
                }
            },
            // 
            setListData(result){
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/table_list";
    .query-item{
        width: 150px;
    }
</style>


